


* {
    font-family: Poppins;
}

body {
    background-color: #FFF7F4;
}

ul {
    list-style: none;
}

.navitem {
    color: black;
}

.talk-btn:hover,
.navitem:hover {
    color: #FF5300
}

.talk-btn {
    background-color: #FFF7F4;
    border-radius: 5px;
    border-width: 1px;
}

.start {
    background-color: #FF5300;
    border-radius: 5px;
    border: none;
}
.dash {
    width: 55px;
    margin: 0px;
    border: 1px solid #FF5300;
    display: inline-block;
  }

  .services-head {
    margin: auto;
    text-align: center;

  }

  .service {
    color: #FF5300;
    font-weight: 400;
    font-size: 16px;
  }

  .to-do {
    font-weight: 600;
    font-size: 39.0625px;
  }

  .specific{
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.1);
    border: none;
    
  }
  .num {
    font-weight: 600;
    font-size: 64px;
    color: #EFEFEF;
  }

  .services {
    width: 370px;
  }
   .stream{
    font-size: 25px;
    font-weight: 600;
    color: #373737;

  }
  .service-content{
    font-weight: 400;
    font-size: 16px;
    margin: 0px;
    color: #7B7B7B;
  }
  .compatibility {
    font-weight: 400;
    font-size: 16px;
    margin: 0px;
    color: #7B7B7B;

  }
  .main-slider {
    background-color: #FFF7F4;
}

.specific-feedback {
    background-color: #FFFFFF;
}

.feedback-name {
    color: #FF5300;
    margin: 0px;

    font-weight: 500;
    font-size: 20px;
}

.slides {
    color: #7B7B7B;
}

.feedback-content {
    font-weight: 400;
    font-size: 16px;
}

.feedback-position {
    font-weight: 300;
    font-size: 12.8px;
}
.direction{
    margin-top: 90px;
}